import Toast from '../../wxcomponents/vant/toast/toast';
const db = wx.cloud.database();
const appointment = db.collection('appointment');

export default {
	data() {
		return {
			data: {
				title: '',
				name: '',
				childName: '',
				childAge: '',
				phone: '',
				date: '',
				tag: 0,
				desc: ''
			},
			show: false,
			errMsg: '',
			errorMsg: '',
			date:'',
			currentDate: new Date().getTime(),
			minDate: new Date().getTime(),
		};
	},
	methods: {
		submit(){
			if(this.data.name == '' || this.data.phone == '' || this.errMsg != '' || this.errorMsg != ''){
				Toast('信息填写不完整')
			}else{
				 Toast('预约成功')
				 const submitData = this.data
				appointment.add({
				  // data 字段表示需新增的 JSON 数据
				  data: submitData
				})
				.then(res => {
				  uni.switchTab({
				      url: '/pages/home/home'
				  })
				})
				this.data = {
					title: '',
					name: '',
					childName: '',
					childAge: '',
					phone: '',
					date: '',
					desc: '',
					tag: 0
				}
			}
		},
		onOpen(){
			this.show = true
		},
		onClose(){
			this.show = false
		},
		onConfirm(){
			this.show = false
			this.data.date = this.formatter(this.currentDate)
		},
		inputName(event){
			this.data.name = event.detail
		},
		inputPhone(event){
			this.data.phone = event.detail
		},
		inputChildName(event){
			this.data.childName = event.detail
		},
		inputChildAge(event){
			this.data.childAge = event.detail
		},
		checkPhone(event){
			const TEL_CHECK = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
			if(TEL_CHECK.test(event.detail.value)){
				this.errorMsg = ''
			}else{
				this.errorMsg = '请输入正确的手机号'
			}
		},
		checkName(event){
			if(event.detail.value == ''){
				this.errMsg = '姓名不能为空'
			}else{
				this.errMsg = ''
			}
		},
		onInput(event) {  
		    this.currentDate = event.detail
		  },
		formatter(date) {
		      const time = new Date(date);
		      const y = time.getFullYear();
		      const m = time.getMonth()+1;
		      const d = time.getDate();
		      return y+'年-'+ m +'月-'+ d + '日'
		      }
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		        console.log(option.id,option.title); //打印出上个页面传递的参数。
				this.data.title = option.title
	}
}
